<template>
  <div class="flex a-center j-center pt-05 pb-2">
    <img v-if="loadmore=='loading'"
     class="mr-2"
    src="../assets/images/loading.gif" style="width: 60px;height: 60px;"></img>
    <div class="fz-16 cor-9d9 fw-4"
    >{{loadText}}</div>
  </div>
</template>

<script>
  export default {
    name:"loadMore",
    props: {
      obj: {
        type: Object,
        default: ()=>{
         return {
           loadmore: '上拉加载更多',
           loading: '加载中...',
           nomore: "没有更多了哦~"
         }
        }
      },
      loadmore: {
        type: String,
        default: 'loadmore'
      }
    },
    computed: {
      loadText() {
        return this.obj[this.loadmore]
      }
    },
    data() {
      return {
        
      };
    }
  }
</script>


