<template>
  <div>
    <div class="coopen">
      <div class="jump fz-20" @click="jump">
        {{time}}
      </div>
      <div class="tip animate__animated animate__fast animate__bounceInUp">欢迎曾宝宝老师~</div>
    </div>
  </div>
</template>
<script>
  import { mapMutations } from 'vuex'
  export default {
    data() {
      return {
        timer: null,
        time: 5
      }
    },
    methods: {
      ...mapMutations("common",["setCoopenFlag"]),
      init() {
        this.time =5;
        clearInterval(this.timer)
        this.setCoopenFlag(false)
      },
      countDown() {
        if(this.timer) clearInterval(this.timer)
        this.timer = setInterval(()=>{
          this.time--
          if(this.time <=0) {
            this.init()
          }
        },1000)
      },
      jump() {
        this.init()
      }
    },
    mounted() {
      this.countDown()
    }
  }
</script>
<style scoped>
  .coopen {
    position: fixed;
    top: 0;
    right:0 ;
    bottom: 0;
    left: 0;
    background: url('../assets/images/cover.jpg') no-repeat center/cover;
    z-index: 200000;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .jump {
    width:45px;
    height:45px;
    line-height:45px;
    text-align: center;
    border-radius:50%;
    background:rgba(0,0,0,0.4);
    position:fixed;
    top:20px;
    right:20px;
    z-index:1000;
    cursor:pointer;
    user-select:none;
    overflow:hidden;
    white-space:nowrap;
    color:#ffffff;
    letter-spacing:1px;
    font-family:'微软雅黑';
  }
  .tip {
    width: 200px;
    height: 120px;
    background-color: rgba(0,0,0,0.3);
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    font-size: 20px;
    white-space: nowrap;
  }
</style>