        {title:'二年级',icon:'iconfont icon-two',color:'#f60',link:'/banana'},
        {title:'三年级',icon:'iconfont icon-three',color:'#f60',link:'/banana'},
        {title:'四年级',icon:'iconfont icon-four',color:'#f60',link:'/banana'},
        {title:'五年级',icon:'iconfont icon-eight',color:'#f60',link:'/banana'},
        {title:'六年级',icon:'iconfont icon-five',color:'#f60',link:'/banana'},
        {title:'班会',icon:'iconfont icon-nine',color:'#f60',link:'/banana'},
        {title:'活动',icon:'iconfont icon-six',color:'#f60',link:'/banana'},
      ],
    })
    return {
      ...toRefs(state)
    }
