<template>
    <div>
      <!-- 开屏 -->
      <Coopen v-if="coopenFlag"></Coopen>
      <!-- 灯笼 -->
      <DengLong></DengLong>
      <!-- 搜索 -->
      <search-page></search-page>
      <!-- 轮播图-->
      <Carousel></Carousel>
      <!-- 导航‘九宫格’展示 -->
      <Nav></Nav>
      <!-- 图文列表 -->
      <Lists></Lists>
    </div>
</template>

<script>
import Nav from '../components/Nav'
import Lists from '../components/Lists'
import Carousel from '../components/Carousel'
import { mapState } from 'vuex'
export default {
  name: 'Home',
  provide() {
    return {
      imgArr:this.imgArr
    }
  },
  components:{
    Nav,
    Lists,
    Carousel,
  },
  data() {
    return {
      imgArr:[
      //webpack解析,不会添加对应的地址，需要加上require
        require( '../assets/images/banner-1.png'),
        require( '../assets/images/banner-2.png'),
      ],
    }
  },
  //coopenFlag
  computed:{
    ...mapState({
      coopenFlag: state=> state.common.coopenFlag
    })
  },
  mounted(){
    document.documentElement.getElementsByTagName('body')[0].style.background="#f2f2f2"
  },
  beforeDestroy() {
  }
}
</script>
<style scoped>
</style>


