<template>
  <div class="lists pb-10">
    <template  v-if="lists.length">
      <div
        
        class="item animate__animated animate__fast  animate__fadeIn" 
        v-for="(item,index) in lists" 
        :key="index"
      >
        <div class="img-box"  @click="jumpDetail(item)">
          <img :src="item.img" >
        </div>
        <p class="title">{{item.title}}</p>
        <div class="d-flex a-center ml-12 fz-16 cor-666">
          {{item.desc}}
        </div>
      </div>
      <div class="w-percent100" v-if="lists.length" >
        <load-more :loadmore="loadmore"></load-more>
      </div>
    </template>
  </div>
</template>

<script>
import {getList} from '@/utils/common.js'
import { arr } from '@/utils/index.js'
const listArr = [...arr]
export default {
  data() {
    return {
      lists:[],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      loadmore: 'loading'
    }
  },

  methods: {
    getData() {
      let params = {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        keywords: null
      }
      getList(params,listArr).then(res=>{
        let { code,list,total } = res
        this.lists = this.pageNum == 1 ? [...list] : [...this.lists,...list];
        this.total = total
        let loadTotal = Math.ceil(this.lists.length / this.pageSize);
        this.loadmore = loadTotal >= total ? 'nomore' : 'loadmore'
      })
    },
    initData() {
      for(let i=0;i<20;i++) {
        listArr.push(...arr)
      }
      console.log(listArr)
    },
    jumpDetail(item) {
      this.$router.push({
        name: 'Detail',
        query: {
          item
        }
      })
    },
    handleScroll() {
      // 滚动距离
      let top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
      // var scrollTop = Math.ceil(top);  
      let scrollTop = Math.ceil(top);    
      // 可视区高度
      var clientH = document.documentElement.clientHeight;
      // 页面总高度
      var totalHeight = document.body.scrollHeight;
      if(scrollTop + clientH >= totalHeight -100){ // 滚动
        if (this.loadmore !== 'loadmore') return;
        if (this.pageNum < this.total) {
          this.pageNum++
          this.getData()
        }
      }
    }
  },
  mounted () {
    this.initData()
    this.$nextTick(()=>{
      this.getData()
    })
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestory () {
    // 离开该页面需覝移除这个监坬的事件，不然会报错
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped>
.lists {
  display:flex;
  flex-wrap:wrap;
  max-width:640px;
  font-size:16px;
  margin:3% 0 16% 3%;
  /* background:red; */
  cursor: pointer;
}
/* 图文列表还可以使用计算属性,calc(100% / 2 - 24px) */
@media screen and (min-width:640px){
  .lists {
    display:flex;
    max-width:640px;
    font-size:16px;
    flex-wrap:wrap;
    margin:3% auto 5% auto;
  }
}
.item {
  width:47%;
  margin:0 3% 3% 0;
  border-radius:4px;
  overflow:hidden;
  background:#ffffff;
  padding-bottom:12px;
}
.img-box {
  width:100%;
  min-height:200px;
}
.img-box img {
  width:100%;
  min-height:180px;
}
.title {
  padding:12px;
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
}
.price {
  padding:0 12px 0 12px;
}
.new-price {
  color:#f60;
  font-weight:bold;
}
.old-price {
  text-decoration:line-through;
  margin-left:5px;
  font-size:12px;
}
</style>
